<template>
  <div class="container">
    <el-row type="flex" justify="center">
      <!--  -->
      <el-col :span="6">
        <el-card>
          <el-row>
            <el-row>
              <el-button type="primary" round style="width: 92%"></el-button>
            </el-row>
              <br />
            <el-button></el-button>
            <el-select v-model="taskFromValue" placeholder="">
              <el-option
                v-for="item in comTaskFrom"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-row>
          <br />
          <el-row>
            <el-button></el-button>
            <el-select v-model="taskTitileValue" placeholder="">
              <el-option
                v-for="item in tasktitles"
                :key="item.value"
                :label="item.label"
                :value="item.value"
                :disabled="item.disabled"
              ></el-option>
            </el-select>
          </el-row>
          <br />
          <el-row>
            <el-button></el-button>
            <el-select v-model="isLang" placeholder="">
              <el-option
                v-for="item in langList"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              ></el-option>
            </el-select>
          </el-row>
          <br />
          <el-row>
            <el-button round style="width: 92%">
              <em>:</em>
              {{ publicDate | filterTime }}
            </el-button>
          </el-row>
          <br />
          <el-row>
            <el-button type="danger" round style="width: 92%">
              <em>:</em>
              {{ deadLine | filterTime }}
            </el-button>
          </el-row>
        </el-card>
        <el-row>
          <el-card class="task-detail">
            <el-button type="primary" round style="width: 92%"></el-button>
            <el-row>{{ taskDetailValue }}</el-row>
          </el-card>
        </el-row>
      </el-col>
      <el-col :span="16">
        <CodeEditor :propsInfo="comPropsInfo" />
      </el-col>
    </el-row>
  </div>
</template>

<script>
import CodeEditor from "./CodeEditor";
export default {
  name: "Card",
  components: {
    CodeEditor
  },
  data() {
    return {
      taskFromValue: "", //
      taskTitileValue: "", // 
      taskDetailValue: "", // 
      publicDate: "", // 
      deadLine: "", // 
      isDeadLine: undefined, // 
      tasktitles: [], // admin
      tasksList: [], //admin
      langList: [
        // codeEditor mode
        {
          value: "javascript",
          label: "javascript"
        },
        {
          value: "python",
          label: "python"
        },
        {
          value: "clike",
          label: "clike"
        },
        {
          value: "java",
          label: "java"
        }
      ],
      isLang: "javascript",
      resTaskList: [] //ajax 
    };
  },
  computed: {
    comTaskFrom() {
      let taskFromList = [];
      for (let i = 0; i < this.resTaskList.length; i++) {
        let oneTaskFrom = {};
        oneTaskFrom.value = this.resTaskList[i].adminId;
        oneTaskFrom.label = this.resTaskList[i].adminId;
        taskFromList.push(oneTaskFrom);
      }
      return taskFromList;
    },
    comPropsInfo() {
      let propsInfo = {
        taskFrom: this.taskFromValue,
        taskTitle: this.taskTitileValue,
        taskDetail: this.taskDetailValue,
        isLang: this.isLang,
        isDeadLine: this.deadLine
      };
      return propsInfo;
    }
  },

  watch: {
    taskFromValue: {
      handler(newVal, oldVal) {
        // ,
        console.log("watch taskFromValue", newVal);
        if (newVal) {
          this.tasktitles = [];
          this.tasksList = [];
          for (let i = 0; i < this.resTaskList.length; i++) {
            if (this.taskFromValue === this.resTaskList[i].adminId) {
              this.tasksList = this.resTaskList[i].tasks;
            }
          }
          for (let j = 0; j < this.tasksList.length; j++) {
            let oneTaskTitle = {};
            oneTaskTitle.value = this.tasksList[j].taskTitle;
            oneTaskTitle.label = this.tasksList[j].taskTitle;
            this.tasktitles.push(oneTaskTitle);
          }
          return this.tasktitles;
        }
      }
    },
    taskTitileValue: {
      // 
      handler(newVal, oldVal) {
        if (newVal) {
          console.log("watch taskTitle", newVal);
          for (let i = 0; i < this.tasksList.length; i++) {
            if (this.taskTitileValue === this.tasksList[i].taskTitle) {
              this.taskDetailValue = this.tasksList[i].taskDetail;
              this.publicDate = this.tasksList[i].publicDate;
              this.deadLine = this.tasksList[i].deadLine;
              this.isDeadLine = this.tasksList[i].isDeadLine;
              return true;
            }
          }
        }
      },
      deep: true
    }
  },

  mounted() {
    this.$axios({
      url: "",
      method: "post",
      data: { msg: "postAdminTask" }
    })
      .then(res => {
        if (res.data.err === 0) {
          this.$message({
            message: "",
            type: "success"
          });
          return (this.resTaskList = res.data.msg);
        } else {
          this.$message({
            message: "",
            type: "err"
          });
        }
      })
      .catch();
  },
  methods: {
    handleback() {
      this.$router.push({ path: '/stuHome'});
    },
  }
};
</script>

<style lang="scss" scoped>
.container {
  .task-detail {
    height: 500px;
  }
  .el-icon-s-home {
      font-size: 300%;
      cursor: pointer;
    color: #409eff;
    }
  
}
</style>